// import useInput from '../../../hooks/useInput';
import { useFormik } from "formik";
import { Button, Modal } from "react-bootstrap";

function ProductForm({ isShow,handleClose, handleAddProduct }) {
  const formik = useFormik({
    initialValues: {
      productTitle: "",
      productPrice: "",
      type: "",
      amount: "",
    },

    onSubmit: (values) => {
      const data = {
        productTitle: values.productTitle,
        productPrice: values.productPrice,
        type: values.type,
        amount: 1,
      };

      handleAddProduct(data);
    },
  });



  return (
    <>
      <Modal size="xl" show={isShow} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title><i class="far fa-shopping-cart"></i> Modal headingddsdsdsdsds</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <form onSubmit={formik.handleSubmit}>
            <div className="product-form">
              <div>
                <label>Tên sản phẩm</label>
                <input
                  type="text"
                  value={formik.values.productTitle}
                  onChange={formik.handleChange}
                  name="productTitle"
                />
              </div>
              <div>
                <label>Giá sản phẩm</label>
                <input
                  type="text"
                  value={formik.values.productPrice}
                  onChange={formik.handleChange}
                  name="productPrice"
                />
              </div>
              <div>
                <label>Loại sản phẩm</label>
                <div className="type-select">
                  <select
                    name="type"
                    style={{ width: 75 }}
                    value={formik.values.type}
                    onChange={formik.handleChange}
                  >
                    <option value="MEN">Nam</option>
                    <option value="WOMEN">Nữ</option>
                  </select>
                </div>
              </div>
              <button>Thêm sản phẩm</button>
            </div>
          </form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleClose}>
            Save Changes
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default ProductForm;
